import Vue from 'vue';

export const shopItemBuyed = {
    namespaced: true,
    state: {
        data: [],
        openItems: null
    },
    getters: {
        data: state  => state.data
    },
    mutations: {
        "ADD": (state, shopItem) => {
            state.data.push(shopItem);
        },
        "DRESS": (state, shopItem) => {
            const currentIndex = state.data.findIndex(d => d.id == shopItem.id);
            Vue.set(state.data[currentIndex], 'active', !state.data[currentIndex].active);
            if(state.data[currentIndex].active) {
                for(let i = 0; i < state.data.length; ++i) {
                    if(i == currentIndex) continue;
                    if(state.data[i].pos == shopItem.pos) {
                        Vue.set(state.data[i], 'active', false);
                    }
                }
            }
        },
        "USE": (state, shopItem) => {
            const index = state.data.findIndex(d => d.id == shopItem.id);
            state.data.splice(index, 1);
        },
        "TRIGGER_OPEN": (state, open) => {
            state.openItems = open;
        },
    },
    actions: {
        "ADD": async (store, shopItem) => {
            const {commit} = store;
            commit("ADD", shopItem);
        },
        "DRESS": async (store, shopItem) => {
            const {commit} = store;
            commit("DRESS", shopItem);
        },
        "USE": async (store, shopItem) => {
            const {commit} = store;
            commit("USE", shopItem);
        },
        "TRIGGER_OPEN": async (store, open) => {
            const {commit} = store;
            commit("TRIGGER_OPEN", open);
        },
    }
}